import { useEffect, useState } from "react";
import Slider from "react-slick";
import { Box, Image, Center, Spinner, Text } from "@chakra-ui/react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default function ProductCarousel() {
  const [images, setImages] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch("/products")
      .then(res => res.json())
      .then(data => {
        // Se a resposta for um objeto com 'data', ajuste para data.data
        const produtos = Array.isArray(data) ? data : data.data || [];
        setImages(produtos.map(prod => prod.image));
        setLoading(false);
      })
      .catch(() => setLoading(false));
  }, []);

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 1,
    arrows: true,
    autoplay: false,
    responsive: [
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 1,
        },
      },
    ],
  };

  if (loading) {
    return <Center py={10}><Spinner size="xl" /></Center>;
  }

  if (!images.length) {
    return <Center py={10}><Text>Nenhuma imagem encontrada.</Text></Center>;
  }

  return (
    <Box maxW="900px" mx="auto">
      <Slider {...settings}>
        {images.map((src, idx) => (
          <Center key={idx} py={6}>
            <Image
              src={src.startsWith("/") ? `${process.env.NEXT_PUBLIC_API_URL || ""}${src}` : src}
              alt={`Produto ${idx + 1}`}
              boxSize="180px"
              objectFit="cover"
              borderRadius="100%"
              boxShadow="lg"
            />
          </Center>
        ))}
      </Slider>
    </Box>
  );
}